:export {
  noDataGray: #b3b3b3;
  outbreakBlue2: #125088;
  outbreakBlue3: #457daf;
  outbreakBlue4: #2f85d1;
  outbreakBlue5: #8fc9fb; }

[class*="table-bordered"] {
  border: unset; }

.tableInstance td,
.tableInstance th {
  background-color: white;
  vertical-align: middle !important; }

.tableInstance th {
  text-align: left;
  vertical-align: middle;
  margin: auto 0;
  background-color: #f2f2f2; }

.tableInstance.noColClick th {
  pointer-events: none; }

.tableInstance li > a,
.tableInstance li[class="disabled"] > a {
  color: #333;
  background-color: transparent;
  border: none; }

.tableInstance li[class="disabled"] > a:hover,
.tableInstance li[class="disabled"][class="disabled"] > a:hover {
  background-color: transparent; }

.tableInstance [id="page-menu"] {
  margin-left: 10px;
  margin-bottom: 10px; }

.tableInstance [id="search-field"] {
  margin-bottom: 10px; }

.tableInstance.noNativeSorting [class^="sort-icon"] {
  display: none; }

.tableInstance.noNativePaging [class^="pagination"],
.tableInstance.noNativePaging [for="page-menu"],
.tableInstance.noNativePaging [id="page-menu"] {
  display: none; }

.tableInstance.noNativePageSizeSelect [for="page-menu"],
.tableInstance.noNativePageSizeSelect [id="page-menu"] {
  display: none; }

.tableInstance.noNativeSearch [for="search-field"],
.tableInstance.noNativeSearch [id="search-field"] {
  display: none; }

.tableInstance.origin th {
  background-color: #384434;
  color: white; }

.tableInstance.target th {
  background-color: #380a5c;
  color: white; }

.tableInstance table {
  border-collapse: separate;
  border-spacing: 0; }
  .tableInstance table tr th,
  .tableInstance table tr td {
    padding: 10px 20px;
    border: 1px solid #ddd;
    border-width: 1px 0; }
  .tableInstance table tr td {
    background-color: white; }
  .tableInstance table tr th {
    background-color: #efefef;
    border-bottom-width: 0;
    cursor: pointer; }
  .tableInstance table tr:first-child td {
    border-top: none; }
  .tableInstance table tr:not(:last-child) td {
    border-bottom-width: 0; }
  .tableInstance table tr td:first-child,
  .tableInstance table tr th:first-child {
    border-left-width: 1px; }
  .tableInstance table tr td:last-child,
  .tableInstance table tr th:last-child {
    border-right-width: 1px; }

.tableInstance table tr:first-child th:first-child {
  border-top-left-radius: 10px; }

.tableInstance table tr:first-child th:last-child {
  border-top-right-radius: 10px; }
