/* Gotham Bold */
@font-face {
  font-family: "Gotham Bold";
  font-style: normal;
  font-weight: normal;
  src: local("Gotham Bold"),
    url("../fonts/gotham/bold/Gotham Bold.woff") format("woff");
}

/* Gotham Book */
@font-face {
  font-family: "Gotham Book";
  font-style: normal;
  font-weight: normal;
  src: local("Gotham Book"),
    url(../fonts/gotham/book/GOTHAMBOOK.woff) format("woff");
}

/* Open Sans */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  src: local("Open Sans"), local("OpenSans-Regular"),
    url(../fonts/openSans/regular/OpenSans-Regular.ttf) format("ttf"),
    url(../fonts/openSans/regular/OpenSans-Regular.woff2) format("woff2"),
    url(../fonts/openSans/regular/OpenSans-Regular.woff) format("woff"),
    url(../fonts/openSans/regular/OpenSans-Regular.eot) format("eot");
}

@font-face {
  font-family: "open_sanssemibold";
  src: url("../fonts/openSans/semibold/OpenSans-Semibold.eot");
  src: url("../fonts/openSans/semibold/OpenSans-Semibold.eot?#iefix")
      format("embedded-opentype"),
    /* url('../fonts/openSans/semibold/OpenSans-Semibold.woff2') format('woff2'), */
