@import "../../../assets/styles/colors.scss";

.sourceText {
  font-size: 0.9em;
  text-decoration: underline;
  margin: 10px 0 0 auto;
}
.sourceTextList {
  list-style-type: none;
  padding-left: 0;
  margin-top: 10px;
  @include spaceChildrenVert(10px);
}
.custom {
  font-size: 1.2rem;
  font-style: italic;
  margin-top: 10px;
  color: #828282;
}
.showAll {
  text-align: center;
  margin: 0 auto;
  color: $outbreak-blue-2;
  &:hover {
    text-decoration: underline;
    cursor: pointer;
  }
  span.flipped {
    transform: rotate(180deg);
  }
}
.url {
  word-break: break-all;
}
