[class*="__react_component_tooltip"].show {
  opacity: 1 !important; }

[class*="__react_component_tooltip"].container {
  padding: 0;
  opacity: 1 !important;
  background-color: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }
  [class*="__react_component_tooltip"].container,
  [class*="__react_component_tooltip"].container > .popup {
    border-radius: 5px; }
  [class*="__react_component_tooltip"].container .popup {
    opacity: 1;
    max-width: 25vw;
    min-width: 400px; }
    [class*="__react_component_tooltip"].container .popup .header,
    [class*="__react_component_tooltip"].container .popup .body {
      padding: 15px; }
    [class*="__react_component_tooltip"].container .popup .header {
      background-color: #f2f2f2;
      border-bottom: 1px solid #c4c4c4;
      display: grid;
      grid-template-columns: auto auto;
      grid-gap: 1rem 2rem;
      align-items: center; }
      [class*="__react_component_tooltip"].container .popup .header .label {
        justify-self: flex-end; }
        [class*="__react_component_tooltip"].container .popup .header .label.isRole {
          height: 30px; }
          [class*="__react_component_tooltip"].container .popup .header .label.isRole.funder {
            width: 105px; }
          [class*="__react_component_tooltip"].container .popup .header .label.isRole.recipient {
            width: 120px; }
      [class*="__react_component_tooltip"].container .popup .header .title {
