    [class*="__react_component_tooltip"].container .popup .body {
      display: grid;
      grid-template-columns: auto auto;
      grid-gap: 1rem 2rem;
      font-size: 1.6rem; }
      [class*="__react_component_tooltip"].container .popup .body .label {
        color: #828282;
        font-family: "open_sanssemibold", sans-serif;
        text-align: right; }
      [class*="__react_component_tooltip"].container .popup .body .value {
        color: black;
        font-weight: normal;
        font-family: "open_sanssemibold", sans-serif; }
    [class*="__react_component_tooltip"].container .popup.left .body,
    [class*="__react_component_tooltip"].container .popup.left .header {
      display: flex; }
    [class*="__react_component_tooltip"].container .popup.left .body .label {
      text-align: left; }
