.menu {
  left: -1px;
  top: 30px;
  position: absolute;
  width: max-content;
  z-index: 1000;
  background-color: white;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175); }
  .menu.inSubMenu {
    max-width: 250px;
    left: calc(100% - 2px);
    top: 0; }
    .menu.inSubMenu a {
      white-space: normal; }
  .menu .links {
    display: flex;
    flex-flow: column; }
    .menu .links > a,
    .menu .links > section > div:first-child {
      display: flex;
      align-items: center;
      color: #303434;
      text-decoration: none;
      padding: 10px;
      font-size: 0.9em; }
      .menu .links > a:hover, .menu .links > a.active,
      .menu .links > section > div:first-child:hover,
      .menu .links > section > div:first-child.active {
        background-color: #f5f5f5; }
  .menu.dark {
    background-color: #222;
    border: 1px solid #ccc; }
    .menu.dark a:hover, .menu.dark a.active,
    .menu.dark section > div:first-child:hover,
    .menu.dark section > div:first-child.active {
      background-color: #333; }

[class^="scrollbar-container"] {
