@import "../../../assets/styles/colors.scss";

.export {
  .header {
    @include header;
  }
  .title {
    @include title;
  }
  .filters {
    margin-top: 5px;

    width: 100%;
    display: flex;
    flex-flow: row;
    align-items: center;
    > div {
      margin: 0 10px;
      &:first-child {
        margin-left: 0;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .checkboxes {
    margin-top: 5px;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    justify-content: start;
    grid-auto-flow: column;
    grid-gap: 0 20px;
  }
  .sectionContent {
    display: flex;
    flex-flow: row;
    align-items: flex-end;
    justify-content: space-between;
  }
  .sectionHeader {
    display: flex;
    flex-flow: row;
    align-items: flex-end;
    justify-content: space-between;
  }
  .downloadBtn {
    display: flex;
    align-items: center;
  }
}
