@import "../../../../../../assets/styles/colors.scss";
.map {
  position: relative;
  display: flex;
  flex-flow: column;
  height: 100%;
  max-height: $min-content-height;

  // align-items: center;
  > div {
  }
  .square {
    width: 20px;
    height: 20px;
  }
  .hatch {
    mask: url(#mask-stripe);
  }
  .legend {
    position: absolute;
    bottom: 0;
    align-self: center;

    > div {
      display: flex;
      flex-flow: column;
      align-items: center;
      // div:nth-child(2) {
      //   // flex-flow: column;
      //   align-items: center;
      // }
    }
  }
  .infoBox {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 25%;
  }
}
